import React from 'react';
import { Card, CardActions, CardContent, CardMedia, Button, Typography } from '@material-ui/core';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import DeleteIcon from '@material-ui/icons/Delete';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import moment from 'moment';
import useStyles from './styles.js';
import { useDispatch } from 'react-redux';

import { deleteTrail } from '../../../actions/trails.js';
 
const Trail = ({ trail, setCurrentId }) => {
    const classes = useStyles();
    const dispatch = useDispatch();

    return (
        <Card className={classes.card}>
            <CardMedia className={classes.media} image={trail.selectedFile} title={trail.title} />
            <div className={classes.overlay}>
                <Typography variant="h6">{trail.creator}</Typography>
                <Typography variant="body2">{moment(trail.createdAt).fromNow()}</Typography>
            </div>
            <div className={classes.overlay2}>
                <Button style={{ color: 'white' }} size="small" onClick={() => setCurrentId(trail._id)}><MoreHorizIcon fontSize="medium" /></Button>
            </div>
            <div className={classes.details}>
                <Typography variant="body2" color="textSecondary" component="h2"></Typography>
            </div>
                <Typography className={classes.title} gutterBottom variant="h5" component="h2">{trail.title}</Typography>
            <CardContent>
                <Typography variant="body2" color="textSecondary" component="p">{trail.location}</Typography>
                <Typography variant="body2" color="textSecondary" component="p">{trail.distance} mi</Typography>
            </CardContent>
            <CardActions className={classes.cardActions}>
                <Button size="small" color="primary" onClick={() => dispatch(deleteTrail(trail._id))}><DeleteIcon fontSize="small" /> Delete </Button>
            </CardActions>
        </Card>
    );
}

export default Trail;